import React, { Component } from "react";
import "./Card.css";

class Card extends Component {
  constructor(props) {
    super(props);
    let angle = Math.random() * 90 - 45;
    let xPos = Math.random() * 40 - 20;
    let yPos = Math.random() * 40 - 20;
    this._transform = `translate(${xPos}px, ${yPos}px) rotate(${angle}deg)`; //如果放在render里，每一次添加之前的也会重新随机重新渲染
  }
  render() {
    return (
      <img
        className="Card"
        style={{ transform: this._transform }}
        src={this.props.image}
        alt={this.props.name}
      />
    );
  }
}

export default Card;
