import React, { Component } from "react";
import axios from "axios";
import Card from "./Card";
import "./Deck.css"

const BASE_API_URL = "https://deckofcardsapi.com/api/deck/";

class Deck extends Component {
  constructor(props) {
    super(props);
    this.state = { deck: null, drawn: [] };
    this.getCard = this.getCard.bind(this);
  }
  async componentDidMount() {
    let deck = await axios.get(`${BASE_API_URL}new/shuffle/`);
    this.setState({ deck: deck.data });
  }
  async getCard() {
    try {
      let cardUrl = `${BASE_API_URL}/${this.state.deck.deck_id}/draw/`;
      let cardResponse = await axios.get(cardUrl);
      if (!cardResponse.data.success) {
        throw new Error("No card remaining!");
      }
      let card = cardResponse.data.cards[0];
      this.setState(st => ({
        drawn: [
          ...st.drawn,
          { id: card.code, img: card.image, name: `${card.suit} ${card.value}` }
        ]
      }));
    } catch (err) {
      alert(err);
    }
  }
  render() {
    const cards = this.state.drawn.map(c => (
      <Card key={c.id} name={c.name} image={c.img} />
    ));
    return (
      <div>
        <h1 className="Deck-title">Card Dealer</h1>
        <h2 className="Deck-title subtitle">A little demo made with React</h2>
        <button className="Deck-btn" onClick={this.getCard}>Get Card!</button>
        <div className="Deck-Cardarea">{cards}</div>
      </div>
    );
  }
}

export default Deck;
